body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.Arrow{position:relative;height:6rem;width:6rem;border:8px solid white;border-radius:50%}.Arrow .Arrow-elem{border:4px solid white;border-radius:8px}.Arrow .Arrow-elem.Arrow-main{position:absolute;top:10%;bottom:10%;left:calc(50% - 4px)}.Arrow .Arrow-elem.Arrow-ext1{position:absolute;top:35%;bottom:10%;left:calc(50% - 4px);-webkit-transform:rotate(45deg) translateX(-30%);transform:rotate(45deg) translateX(-30%);-webkit-transform-origin:0 100%;transform-origin:0 100%}.Arrow .Arrow-elem.Arrow-ext2{position:absolute;top:35%;bottom:10%;left:calc(50% - 4px);-webkit-transform:rotate(-45deg) translateX(30%);transform:rotate(-45deg) translateX(30%);-webkit-transform-origin:100% 100%;transform-origin:100% 100%}

.Menu{height:2rem;width:2rem;display:flex;flex-direction:column;align-items:center;justify-content:center}.Menu .Menu-bar{width:calc(100% - 8px);border:2px solid #d3d3d3;border-radius:2px}.Menu .Menu-bar.Menu-bar-mid{margin:4px 0}.Menu.ap-icon-color-main .Menu-bar{border-color:#000}.Menu.ap-icon-color-secondary .Menu-bar{border-color:#477052}

.AppHeader{position:fixed;top:0;left:0;right:0;height:49px;background:white;color:black;z-index:2;border-bottom:1px solid lightgrey;padding:0 2rem;display:flex;align-items:center;justify-content:space-between}.AppHeader .AppHeader-area{display:flex;align-items:center}.AppHeader .AppHeader-item{margin:1rem}.AppHeader .AppHeader-item.AppHeader-item-lang{margin:0;height:2rem;border:2px solid grey;border-radius:6px;color:grey;background:none;font-weight:bold;font-style:italic}.AppHeader .AppHeader-item .AppHeader-link{color:#000;color:initial;text-decoration:none}.AppHeader .AppHeader-item .AppHeader-link.AppHeader-link-active{color:#477052;border-bottom:2px solid #477052}.AppHeader .AppHeader-links{display:none;align-items:center}.AppHeader .AppHeader-menu-btn-container{position:relative;height:2rem;width:2rem;cursor:pointer}.AppHeader .AppHeader-menu-btn-container .AppHeader-menu-btn{position:absolute;top:0;left:0;right:0;bottom:0;border:0;margin:0;padding:0;background:transparent}.AppHeader .AppHeader-menu-btn-container .AppHeader-menu-btn-mask{position:absolute;top:0;left:0;right:0;bottom:0}.AppHeader .AppHeader-menu{display:none;position:fixed;top:50px;left:0;right:0;bottom:0;color:black;background:rgba(255,255,255,0.85)}.AppHeader .AppHeader-menu.AppHeader-menu-show{display:block}.AppHeader .AppHeader-menu .AppHeader-menu-list{padding:0;margin:0}.AppHeader .AppHeader-menu .AppHeader-menu-item .AppHeader-menu-link{display:block;color:black;text-decoration:none;padding:0.5rem 0;-webkit-transition:background-color 0.25s;transition:background-color 0.25s}.AppHeader .AppHeader-menu .AppHeader-menu-item .AppHeader-menu-link:hover{background-color:lightgrey}.AppHeader .AppHeader-menu .AppHeader-menu-item .AppHeader-menu-link.AppHeader-menu-link-active{color:#477052;font-weight:bold}@media screen and (min-width: 740px){.AppHeader .AppHeader-links{display:flex}.AppHeader .AppHeader-menu-btn-container{display:none}.AppHeader .AppHeader-menu{display:none !important}}

.Home{position:relative;height:auto}.Home .Home-page{position:relative;height:calc(100vh - 50px);background-color:rgba(255,255,255,0.75);overflow:hidden;padding:0 2rem;display:flex;align-items:center;justify-content:center}.Home .Home-page.Home-page-small{height:50vh}.Home .Home-page .Home-page-main-title{width:15rem;margin:auto}.Home .Home-page .Home-page-bottom{position:absolute;bottom:5%;left:0;right:0;display:flex;justify-content:center}.Home .Home-page .Home-page-background-container{position:absolute;top:0;left:0;right:0;bottom:0;z-index:-1;display:flex;align-items:center;justify-content:center}.Home .Home-page .Home-page-background-container .Home-page-background{height:100%;width:100%;object-fit:cover}

.Services{height:auto}.Services .Services-sections .ServiceSection .ServiceSection-area-background{height:40vh}.Services .Services-sections .ServiceSection .ServiceSection-area-background .ServiceSection-image{height:100%;width:100%;object-fit:cover}.Services .Services-sections .ServiceSection .ServiceSection-area-background .ServiceSection-image.contains{object-fit:contain}.Services .Services-sections .ServiceSection .ServiceSection-area-text .ServiceSection-text-title{margin:2rem;margin-bottom:0}.Services .Services-sections .ServiceSection .ServiceSection-area-text .ServiceSection-text-content{margin:2rem;margin-top:1rem}@media screen and (min-width: 740px){.Services .Services-sections{justify-content:space-around;display:flex}.Services .Services-sections .ServiceSection{width:30%}}

.Projects{height:auto}.Projects .ProjectSection{position:relative;height:calc(100vh - 50px);overflow:hidden}.Projects .ProjectSection.ProjectSection-even{background:#477052;color:white}.Projects .ProjectSection .ProjectSection-area.ProjectSection-text{height:45%;display:flex;flex-direction:column;align-items:center;justify-content:center}.Projects .ProjectSection .ProjectSection-area.ProjectSection-text .ProjectSection-text-title{margin:0 2rem}.Projects .ProjectSection .ProjectSection-area.ProjectSection-text .ProjectSection-text-content{margin:2rem}.Projects .ProjectSection .ProjectSection-area.ProjectSection-background{height:55%}.Projects .ProjectSection .ProjectSection-area.ProjectSection-background .ProjectSection-image{height:100%;width:100%;object-fit:cover}@media screen and (min-width: 740px){.Projects .ProjectSection{height:50vh;display:flex}.Projects .ProjectSection .ProjectSection-area.ProjectSection-text,.Projects .ProjectSection .ProjectSection-area.ProjectSection-background{height:100%;width:50%}.Projects .ProjectSection.ProjectSection-even .ProjectSection-area.ProjectSection-text{position:absolute;right:0;left:50%}.Projects .ProjectSection.ProjectSection-even .ProjectSection-area.ProjectSection-background{position:absolute;left:0;right:50%}}

.Contact{height:100%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.Contact .Contact-title{font-size:1.5rem}.Contact .Contact-content{margin:2rem;width:calc(100% - 4rem)}.Contact .Contact-form{display:flex;align-items:center;flex-direction:column;margin:1rem 0}.Contact .Contact-form .Contact-label{width:100%;margin:0}.Contact .Contact-form .Contact-input,.Contact .Contact-form .Contact-textarea{width:100%}.Contact .Contact-btn{height:3rem;width:calc(100% - 2rem);border:0;border-radius:1.5rem;background-color:#477052;color:white}.Contact .Contact-btn:disabled{background-color:lightgrey;cursor:not-allowed}.Contact .Contact-btn.Contact-btn-sent{background-color:white;pointer-events:none;border:3px solid #477052;color:#477052}.Contact .Contact-btn.Contact-btn-error{background-color:white;pointer-events:none;border:3px solid red;color:red}

.AppFooter{font-size:0.9rem;padding:2rem 0;background:#323232;color:lightgrey}

.AppContent{position:fixed;top:50px;left:0;right:0;bottom:0;overflow:scroll;overflow-x:hidden;color:#477052}

.App{text-align:center}

